@import '../theme.sass';

.container {
    position: relative; }

.input {
    height: 40px;
    font-size: 16px;
    border: $border;
    border-radius: 5px;
    padding: 5px 10px;
    color: $textColor;
    &:focus {
        outline-style: none;
        border-color: darken($borderColor, 20%);
        border-bottom-left-radius: 0; } }

.optionsHolder {
    position: relative; }

.options {
    position: absolute;
    top: 0;
    background: #ffffff;
    border: $border;
    max-height: 200px;
    min-width: 100%;
    overflow-y: auto;
    border-radius: 0 5px 5px 5px; }

.optionItem {
    height: 30px;
    padding: 5px 10px;
    cursor: pointer;
    white-space: nowrap;
    &:hover {
        background: #dddddd; } }

.highlight {
    background: #fdffbc; }

.loading {
    border: 2px dashed #489cc1;
    position: absolute;
    right: 5px;
    top: calc(50% - 10px);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    animation: spin 2s linear infinite; }

@keyframes spin {
 100% {
    transform: rotate(360deg); } }
